body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.navbar {
    background: #185c8a;
    min-height: 70px;
    position: fixed;
    width: 100%;
    box-shadow: 0 14px 28px rgba(0, 0, 0, .25), 0 10px 10px rgba(0, 0, 0, .22);
    color: #fff;
    font-size: calc(10px + 14 * (100vw - 320px)/ 800);
    z-index: 99;
    white-space: wrap;
}

.navbar ul {
    margin: 0;
    padding: 0;
}

.navbar li {
    display: inline-block;
    padding: 26px 0;
    width: 33.3%;
    text-align: center;
}

.navbar a {
    text-decoration: none;
    color: #fff;
    font-weight: 900;
}

.navbar .correct {
    -webkit-animation: 1s correct;
    animation: 1s correct
}

.navbar .incorrect {
    -webkit-animation: 1s incorrect;
    animation: 1s incorrect
}

@-webkit-keyframes correct {
    0% {
        color: #fff
    }

    20% {
        color: #2e7d32
    }

    40% {
        color: #fff
    }

    60% {
        color: #2e7d32
    }

    80% {
        color: #fff
    }

    to {
        color: #fff
    }
}

@keyframes correct {
    0% {
        color: #fff
    }

    20% {
        color: #2e7d32
    }

    40% {
        color: #fff
    }

    60% {
        color: #2e7d32
    }

    80% {
        color: #fff
    }

    to {
        color: #fff
    }
}

@-webkit-keyframes incorrect {
    0% {
        color: #fff
    }

    20% {
        color: #c62828
    }

    40% {
        color: #fff
    }

    60% {
        color: #c62828
    }

    80% {
        color: #fff
    }

    to {
        color: #fff
    }
}

@keyframes incorrect {
    0% {
        color: #fff
    }

    20% {
        color: #c62828
    }

    40% {
        color: #fff
    }

    60% {
        color: #c62828
    }

    80% {
        color: #fff
    }

    to {
        color: #fff
    }
}
.header {
    background-color: #2c323c;
    background-repeat: repeat;
    color: #fff;
    min-height: 110px;
    padding: 190px 20px;
    padding-bottom: 110px;
    text-align: center;
}
.click-item {
    background-size: cover;
    background-position: 50%;
    height: 170px;
    width: 170px;
    margin: 8px 25px;
    border: 5px solid #f1f1f1;
    border-radius: 3px;
    box-shadow: 0 3px 6px rgba(0,0,0,.16), 0 3px 6px rgba(0,0,0,.23);
    cursor: pointer;
}
.footer {
    background: #5a9ac6;
    height: 100px;
    position: relative;
    color: #fff;
    overflow: auto;
}

.footer-bottom {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background: #185c8a;
    padding: 8px;
    font-weight: 600;
}

.footer img {
    width: 20px;
    position: relative;
    top: 3px;
    left: 2px;
}
.content {
  display: -webkit-flex;
  display: flex;
  min-height: 70%;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  padding-top: 50px;
  padding-bottom: 50px;
  width: 90%;
  margin: 0 auto;
}

.App {
  min-width: 350px;
}


